export const PRODUCTION_STATS = {
  totalConnections: 1133,
  generatedDate: '2025-09-27T15:07:09.685Z',
  autoDetected: 467,
  manuallyAdded: 666
};

// ProductionDataManager class for handling production mode
export class ProductionDataManager {
  static async generateProductionDataFile(): Promise<string> {
    const storedData = StorageManager.loadData();
    if (!storedData) {
      throw new Error('No data found to export. Please upload and curate your connections first.');
    }

    // Sanitize data for production (remove sensitive info like emails)
    const sanitizedConnections = storedData.medalliaConnections.map(conn => ({
      ...conn,
      email: '', // Remove emails for privacy
      profileUrl: '', // Remove profile URLs for privacy
      notes: '', // Remove personal notes
      thankYouMessage: conn.thankYouMessage || '', // Keep public thank you messages
      connectedDate: conn.connectedDate.toISOString() // Convert to ISO string for JSON
    }));

    const productionDataTemplate = `import { Connection } from './csvParser';

// Auto-generated production data - DO NOT EDIT MANUALLY
// Generated on: ${new Date().toISOString()}
// Total connections: ${sanitizedConnections.length}

export const PRODUCTION_CONNECTIONS: Connection[] = ${JSON.stringify(sanitizedConnections, null, 2).replace(/"connectedDate": "([^"]+)"/g, '"connectedDate": new Date("$1")')};

export const PRODUCTION_STATS = {
  totalConnections: ${sanitizedConnections.length},
  generatedDate: '${new Date().toISOString()}',
  departments: ${JSON.stringify(this.getDepartmentStats(storedData.medalliaConnections))},
  yearRange: {
    earliest: ${Math.min(...storedData.medalliaConnections.map(c => c.connectedDate.getFullYear()))},
    latest: ${Math.max(...storedData.medalliaConnections.map(c => c.connectedDate.getFullYear()))}
  }
};`;

    return productionDataTemplate;
  }

  static async exportProductionDataFile(): Promise<void> {
    try {
      const dataFileContent = await this.generateProductionDataFile();

      const blob = new Blob([dataFileContent], { type: 'text/typescript' });
      const url = URL.createObjectURL(blob);
      const link = document.createElement('a');
      link.href = url;
      link.download = 'productionData.ts';
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      URL.revokeObjectURL(url);

      console.log('Production data file generated successfully');
    } catch (error) {
      console.error('Failed to generate production data file:', error);
      throw error;
    }
  }

  private static getDepartmentStats(connections: Connection[]): Record<string, number> {
    return connections.reduce((acc, conn) => {
      const dept = conn.department || 'Other';
      acc[dept] = (acc[dept] || 0) + 1;
      return acc;
    }, {} as Record<string, number>);
  }

  static isProductionMode(): boolean {
    // In production, use built-in data if available
    // In development, allow override with environment variable
    return (process.env.NODE_ENV === 'production' || process.env.REACT_APP_USE_PRODUCTION_DATA === 'true') &&
           PRODUCTION_CONNECTIONS.length > 0;
  }

  static getProductionConnections(): Connection[] {
    return PRODUCTION_CONNECTIONS.map(conn => ({
      ...conn,
      connectedDate: new Date(conn.connectedDate)
    }));
  }
}